export function remove_fstrings(code) {
  var counter;
  var list;

  function f2(match, part) {
    var result = "{" + counter + "}";
    counter++;
    list.push(part);
    return result;
  }

  function f1(match, part) {
    counter = 0;
    list = [];
    part = part.replace(/\{(.*?)\}/g, f2);
    return match[1] + part + match[1] + ".format(" + list.join() + ")";
  }

  code = code.replace(/f"(.*?)"/g, f1);
  code = code.replace(/f'(.*?)'/g, f1);
  return code;
}